import React from 'react';
var slovar={' ':' ','a':'ùñÜ','b':'ùñá','c':'ùñà','d':'ùñâ','e':'ùñä','f':'ùñã','g':'ùñå','h':'ùñç','i':'ùñé','j':'ùñè','k':'“ù','l':'ùñë','m':'ùñí',
    'n':'ùñì','o':'ùñî','p':'ùñï','q':'ùññ','r':'ùñó','s':'ùñò','t':'ùñô','u':'ùñö','v':'ùñõ','w':'ùñú','x':'ùñù','y':'ùñû','z':'ùñü','1':'‚Ö†','2':'‚Ö°','3':'‚Ö¢',
    '4':'‚Ö£','5':'‚Ö§','6':'‚Ö•','7':'‚Ö¶','8':'‚Öß','9':'‚Ö®','10':'‚Ö©','L':'‚Ñí'};
function Transpo(props) {
    const text=props.text;
    const texta=text.split('');
    for(let i=0;i<texta.length;i++){
        if(texta[i] in slovar) {
            texta[i] = slovar[texta[i]];
        }
    }
    return <h1>{texta.join('')}</h1>;
}
export default Transpo;